.monkey {
    max-width: 250px;
    margin: 10px;
    border: 5px solid white;
    -webkit-box-shadow: 3px 3px 5px rgba(0,0,0,.6);
            box-shadow: 3px 3px 5px rgba(0,0,0,.6);
}

.monkey:hover {
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
    cursor: pointer;
}


